
import './App.css';
import web3 from './web3'; //aa object thi extually goerila n/w ma comunication thay che ..
import lottery from './lottery';//aa lottery ma aapdo contract no object che.
import { Component } from 'react';


class App extends Component {

  state = { //space na mukvi quets ni vachhe 
    manager: "",
    players: [],
    balance: "",
    value: "",
    message: ""
  }

  async componentDidMount() { //aa automatical call thay jayre app render thay tyare ...
    const manager = await lottery.methods.manager().call();
    const players = await lottery.methods.getPlayersList().call();
    const balance = await web3.eth.getBalance(lottery.options.address);


    this.setState({ manager, players, balance });

  }

  onSubmit = async (event) => {
    event.preventDefault(); //aa form ne submit thata prevent karche .

    const accounts = await web3.eth.getAccounts();

    this.setState({ message: "You are entering in players list just wait couple of secounds...." });

    try {

      await lottery.methods.enter().send({
        from: accounts[0],
        value: web3.utils.toWei(this.state.value, "ether")

      })
    } catch (error) {
      return this.setState({ message: "Error ! Probably internal error" })
    }

    this.setState({ message: "Ok ! You are entered." })
  }

  onClick = async (event) => {

    const accounts = await web3.eth.getAccounts();

    this.setState({ message: "Transaction is running just wait couple of secounds...." });

    try {
      await lottery.methods.pickWinner().send({
        from: accounts[0],
      })

    } catch (error) {

      return this.setState({ message: "Error ! Something error in server " })
    }
    this.setState({ message: "ok ! This is a winner 0x5b240BA1652a289Db35F0cD5446318843B50fB6a" })

  }

  render() {
    return (
      <>
        <div className='container my-2'><p className="text-success text-right">NitinCode</p></div>
        <div className='container my-2'>
          <h2>
            Welcome To Lottery-Zone
          </h2>
          <p>
            This contract is manage by <b>{this.state.manager}</b>.<br></br>
            There are currently <b>{this.state.players.length}</b>  people's entered.<br></br>
            competing to win <b>{web3.utils.fromWei(this.state.balance, 'ether')}</b> ether.
          </p>

          <hr />
          <form onSubmit={this.onSubmit}>
            <h4>
              Want To Try Your Luck?
            </h4>
            <div>
              <label>
                Amount Of Ether To Enter [must be more then <b>`0.00000001`</b> Ether] :
              </label>

              <input type="number" required className='mx-1' value={this.state.value} onChange={event => this.setState({ value: event.target.value })} >

              </input>
            </div>
            <button className='btn btn-primary'>Enter</button>
          </form>
          <hr />
          <h4>You Can Chech Who Win This Game !</h4>
          <button onClick={this.onClick} className='btn btn-primary'>Pick A Winner</button>
          <hr />
          <label><h1>{this.state.message}</h1></label>
        </div>
      </>
    );
  }

}


export default App;
